const [editingProduct, setEditingProduct] = useState<Product | null>(null);

"use client";
import { useEffect, useState } from "react";
import styles from "./adProducts.module.css";

interface Product {
  _id: string;
  name: string;
  price: number;
  image?: string;
}

export default function AdminProducts() {
  const [products, setProducts] = useState<Product[]>([]);
  const [name, setName] = useState("");
  const [price, setPrice] = useState("");
  const [image, setImage] = useState<File | null>(null);

  // Fetch danh sách sản phẩm
  useEffect(() => {
    async function fetchProducts() {
      try {
        const res = await fetch("http://localhost:3450/product");
        const data = await res.json();
        setProducts(data);
      } catch (error) {
        console.error("Lỗi khi tải sản phẩm:", error);
      }
    }
    fetchProducts();
  }, []);

  // Xử lý thêm sản phẩm
  const handleAddProduct = async (e: React.FormEvent) => {
    e.preventDefault();
    const formData = new FormData();
    formData.append("name", name);
    formData.append("price", price);
    if (image) formData.append("image", image);

    try {
      const res = await fetch("http://localhost:3450/product", {
        method: "POST",
        body: formData,
      });
      const newProduct = await res.json();
      setProducts([...products, newProduct]);
      setName("");
      setPrice("");
      setImage(null);
    } catch (error) {
      console.error("Lỗi khi thêm sản phẩm:", error);
    }
  };
  const handleEdit = (product: Product) => {
    setEditingProduct(product);
    setName(product.name);
    setPrice(product.price.toString());
    setImage(null); // Không thay đổi ảnh ban đầu
  };
  
  // Xử lý xóa sản phẩm
  const handleDelete = async (id: string) => {
    try {
      await fetch(`http://localhost:3450/product/${id}`, { method: "DELETE" });
      setProducts(products.filter((product) => product._id !== id));
    } catch (error) {
      console.error("Lỗi khi xóa sản phẩm:", error);
    }
  };

  return (
    <div className={styles.adminContainer}>
      <h1>Quản lý sản phẩm</h1>

      {/* Form thêm sản phẩm */}
      <form className={styles.addProductForm} onSubmit={handleAddProduct}>
        <input type="text" placeholder="Tên sản phẩm" value={name} onChange={(e) => setName(e.target.value)} required />
        <input type="number" placeholder="Giá" value={price} onChange={(e) => setPrice(e.target.value)} required />
        <input type="file" onChange={(e) => setImage(e.target.files?.[0] || null)} />
        <button type="submit">Thêm sản phẩm</button>
      </form>

      {/* Bảng danh sách sản phẩm */}
      <table className={styles.productTable}>
        <thead>
          <tr>
            <th>ID</th>
            <th>Tên sản phẩm</th>
            <th>Giá</th>
            <th>Ảnh</th>
            <th>Hành động</th>
          </tr>
        </thead>
        <tbody>
          {products.map((product) => (
            <tr key={product._id}>
              <td>{product._id}</td>
              <td>{product.name}</td>
              <td>{product.price} VND</td>
              <td>
                {product.image ? <img src={`http://localhost:3450/product/${product.image}`} alt="Ảnh sản phẩm" width="50" /> : "Không có ảnh"}
              </td>
              <td>
                <button className={styles.editBtn} onClick={() => handleEdit(product)}>Sửa</button>
                <button className={styles.deleteBtn} onClick={() => handleDelete(product._id)}>Xóa</button>
              </td>

            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
